import React from 'react';
import './SearchBar';
import './Search.css';
import { SearchBar } from './SearchBar';
import { SearchList } from './SearchList';
export default class Search extends React.Component {
    constructor(props) {
        super(props);
        this.searchSong='';
        this.state={songData:[]};
    }
    componentDidMount(){
        //this.getData();
    }
    getData(searchValue){
        console.log('searchValue',searchValue);
        console.log('searchSong',this.searchSong);
        const url=`https://itunes.apple.com/search?term=${searchValue}&limit=15`;
        const promise=fetch(url);
        promise.then(response=>{
            var p =response.json();
            p.then(data=>{
                console.log('Data :::',data.results)
                this.setState({songData:data.results});
            }).catch(err2=>{
                console.log('Error is JSON ',err2)
            })
            console.log('Response is coming',response)
        }).catch(err=>{
            console.log('ERROR ::: ',err)
        })
    }
    takeInput(event){
        let data = event.target.value
        this.searchSong=data;
        console.log('data : ',this.searchSong)
    }
    searchNow(){
        console.log('button clicked',this.searchSong);
        this.getData(this.searchSong);
    }
    render() {
        return (
            <div className='AppBackground'>
                <h1>
                <div className='MainText'>
                        {this.props.title}
                </div>
                </h1>
                <SearchBar takeInput={this.takeInput.bind(this)} searchNow={this.searchNow.bind(this)}/>
                <SearchList songdata={this.state.songData} />
            </div>
        )
    }
}

