import React from 'react';
import {Song} from './Song';
export const SearchList = (props) => {
    console.log('Props Song data :: ', props.songdata);
    let totalResults = props.songdata.length;
    const noRecord = <p>No Record Found</p>;
    const success = (<>
        <p>Total Results = {totalResults}</p>
        {props.songdata.map(ele => <>
                <Song Name={ele.trackName} Url={ele.previewUrl} />
        </>)}
    </>);
    return (
        <div>
            {props.songdata.length == 0 ? noRecord :  success}
        </div>
    );
}
